import React, { useMemo } from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  Title,
  Tooltip,
  Legend,
  ChartOptions,
} from 'chart.js';
import { Bar } from 'react-chartjs-2';
import { ProcessedData } from '../../types/data';

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  LineElement,
  PointElement,
  Title,
  Tooltip,
  Legend
);

interface SalaryChartProps {
  data: ProcessedData[];
  country: string;
  language: string;
}

export const SalaryChart: React.FC<SalaryChartProps> = ({ data, country, language }) => {
  const chartData = useMemo(() => {
    const labels = data.map(d => d.experienceLevel);
    const medianData = data.map(d => d.stats.median);
    const percentile25Data = data.map(d => d.stats.percentile25);
    const percentile75Data = data.map(d => d.stats.percentile75);

    return {
      labels,
      datasets: [
        {
          label: 'Median Salary',
          data: medianData,
          backgroundColor: 'rgba(59, 130, 246, 0.8)',
          borderColor: 'rgb(59, 130, 246)',
          borderWidth: 1,
        },
        {
          label: '25th Percentile',
          data: percentile25Data,
          backgroundColor: 'rgba(156, 163, 175, 0.6)',
          borderColor: 'rgb(156, 163, 175)',
          borderWidth: 1,
        },
        {
          label: '75th Percentile',
          data: percentile75Data,
          backgroundColor: 'rgba(34, 197, 94, 0.6)',
          borderColor: 'rgb(34, 197, 94)',
          borderWidth: 1,
        },
      ],
    };
  }, [data]);

  const options: ChartOptions<'bar'> = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        position: 'top' as const,
      },
      title: {
        display: true,
        text: `${language} Developer Salaries in ${country} (USD thousands)`,
        font: {
          size: 16,
          weight: 'bold',
        },
      },
      tooltip: {
        callbacks: {
          label: function(context) {
            const dataPoint = data[context.dataIndex];
            return [
              `${context.dataset.label}: $${context.parsed.y}k`,
              `Sample size: ${dataPoint.stats.count} developers`,
              `Range: $${dataPoint.stats.min}k - $${dataPoint.stats.max}k`
            ];
          }
        }
      }
    },
    scales: {
      x: {
        title: {
          display: true,
          text: 'Years of Experience',
          font: {
            weight: 'bold',
          },
        },
      },
      y: {
        title: {
          display: true,
          text: 'Annual Salary (USD thousands)',
          font: {
            weight: 'bold',
          },
        },
        beginAtZero: true,
      },
    },
  };

  if (!data.length) {
    return (
      <div className="bg-white p-8 rounded-lg shadow-md">
        <div className="text-center text-gray-500">
          <p className="text-lg">No data available</p>
          <p className="text-sm mt-2">
            Please select both a country and programming language to view salary data.
          </p>
        </div>
      </div>
    );
  }

  const hasValidData = data.some(d => d.stats.count > 0);
  
  if (!hasValidData) {
    return (
      <div className="bg-white p-8 rounded-lg shadow-md">
        <div className="text-center text-gray-500">
          <p className="text-lg">No salary data found</p>
          <p className="text-sm mt-2">
            No salary information available for {language} developers in {country}.
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white p-6 rounded-lg shadow-md">
      <div className="h-96">
        <Bar data={chartData} options={options} />
      </div>
      
      {/* Statistics Summary */}
      <div className="mt-6 grid grid-cols-2 md:grid-cols-4 gap-4">
        {data.filter(d => d.stats.count > 0).map((item) => (
          <div key={item.experienceLevel} className="text-center p-3 bg-gray-50 rounded">
            <div className="text-xs font-medium text-gray-600 mb-1">
              {item.experienceLevel}
            </div>
            <div className="text-lg font-bold text-blue-600">
              ${item.stats.median}k
            </div>
            <div className="text-xs text-gray-500">
              {item.stats.count} samples
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}; 