import React from 'react';
import { FilterDropdown } from './FilterDropdown';
import { useSalary } from '../../context/SalaryContext';

export const FilterPanel: React.FC = () => {
  const { state, dispatch } = useSalary();

  const handleCountryChange = (country: string) => {
    dispatch({ type: 'SET_COUNTRY', payload: country });
  };

  const handleLanguageChange = (language: string) => {
    dispatch({ type: 'SET_LANGUAGE', payload: language });
  };

  if (!state.filterOptions) {
    return null;
  }

  return (
    <div className="bg-white p-6 rounded-lg shadow-md">
      <h2 className="text-xl font-semibold text-gray-800 mb-6">
        Filter Salary Data
      </h2>
      
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <FilterDropdown
          label="Country"
          value={state.selectedCountry}
          options={state.filterOptions.countries}
          onChange={handleCountryChange}
          placeholder="Select a country"
        />
        
        <FilterDropdown
          label="Programming Language"
          value={state.selectedLanguage}
          options={state.filterOptions.languages}
          onChange={handleLanguageChange}
          placeholder="Select a language"
        />
      </div>

      {state.selectedCountry && state.selectedLanguage && (
        <div className="mt-4 p-3 bg-blue-50 rounded-md">
          <p className="text-sm text-blue-700">
            Showing salary data for <strong>{state.selectedLanguage}</strong> developers in <strong>{state.selectedCountry}</strong>
          </p>
        </div>
      )}
    </div>
  );
};