import React from 'react';
import { SalaryProvider, useSalary } from './context/SalaryContext';
import { FilterPanel } from './components/filters/FilterPanel';
import { SalaryChart } from './components/visualization/SalaryChart';
import './styles/globals.css';

const AppContent: React.FC = () => {
  const { state } = useSalary();

  if (state.loading) {
    return (
      <div className="min-h-screen bg-gray-100 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-600 mx-auto"></div>
          <p className="mt-4 text-lg text-gray-600">Loading salary data...</p>
        </div>
      </div>
    );
  }

  if (state.error) {
    return (
      <div className="min-h-screen bg-gray-100 flex items-center justify-center">
        <div className="text-center bg-white p-8 rounded-lg shadow-md max-w-md">
          <div className="text-red-500 text-6xl mb-4">⚠️</div>
          <h2 className="text-xl font-semibold text-gray-800 mb-2">Error Loading Data</h2>
          <p className="text-gray-600 mb-4">{state.error}</p>
          <button 
            onClick={() => window.location.reload()}
            className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 transition-colors"
          >
            Try Again
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-100">
      {/* Header */}
      <header className="bg-white shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="py-6">
            <h1 className="text-3xl font-bold text-gray-900">
              Developer Salary Calculator
            </h1>
            <p className="mt-2 text-gray-600">
              Interactive visualization of developer salaries from JetBrains Developer Ecosystem Survey 2024
            </p>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="space-y-8">
          {/* Filter Panel */}
          <FilterPanel />

          {/* Chart Visualization */}
          <SalaryChart 
            data={state.processedData}
            country={state.selectedCountry}
            language={state.selectedLanguage}
          />

          {/* Data Summary */}
          {state.processedData.length > 0 && (
            <div className="bg-white p-6 rounded-lg shadow-md">
              <h3 className="text-lg font-semibold text-gray-800 mb-4">
                Data Summary
              </h3>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="text-center p-4 bg-blue-50 rounded">
                  <div className="text-2xl font-bold text-blue-600">
                    {state.processedData.reduce((sum, d) => sum + d.stats.count, 0)}
                  </div>
                  <div className="text-sm text-gray-600">Total Responses</div>
                </div>
                <div className="text-center p-4 bg-green-50 rounded">
                  <div className="text-2xl font-bold text-green-600">
                    ${Math.round(
                      state.processedData
                        .filter(d => d.stats.count > 0)
                        .reduce((sum, d) => sum + d.stats.median, 0) / 
                      state.processedData.filter(d => d.stats.count > 0).length
                    )}k
                  </div>
                  <div className="text-sm text-gray-600">Average Median Salary</div>
                </div>
                <div className="text-center p-4 bg-purple-50 rounded">
                  <div className="text-2xl font-bold text-purple-600">
                    {state.filterOptions?.experienceLevels.length || 0}
                  </div>
                  <div className="text-sm text-gray-600">Experience Levels</div>
                </div>
              </div>
            </div>
          )}
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-white border-t mt-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="text-center text-gray-600">
            <p>Data sourced from JetBrains Developer Ecosystem Survey 2024</p>
            <p className="mt-2 text-sm">
              This interactive calculator allows exploration of developer compensation across different countries, 
              programming languages, and experience levels.
            </p>
          </div>
        </div>
      </footer>
    </div>
  );
};

const App: React.FC = () => {
  return (
    <SalaryProvider>
      <AppContent />
    </SalaryProvider>
  );
};

export default App; 